import React, { useState } from 'react';

import Tree from './Tree';
import logoAbout from '../../assets/images/logo-about.svg';
import './Form.css';

const Form = ({ showFormType, complete }) => {

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [question, setQuestion] = useState('');
    const [submitButtonClicked, setSubmitButtonClicked] = useState(false);
    const [firstCheckboxSelected, setFirstCheckboxSelected] = useState(false);
    const [secondCheckboxSelected, setSecondCheckboxSelected] = useState(false);
    const [thirdCheckboxSelected, setThirdCheckboxSelected] = useState(false);

    const readyToBeSubmitted = ((name.length > 0) && (email.length > 0)) && (((question.length > 0) && (showFormType === 2)) || ((firstCheckboxSelected || secondCheckboxSelected || thirdCheckboxSelected) && (showFormType === 1)));

    let buttonStyle = 'enabled submit absolute form-inputs-width grad-bg pointer white container border-radius';
    if (!readyToBeSubmitted) {
        buttonStyle = 'disabled-btn submit absolute form-inputs-width container border-radius';
    }

    let formClasses = 'hide'
    if (showFormType !== 0) {
        formClasses = 'form no-blur border-radius white-bg flex-column-centered-row show'
    }

    let checkboxesContainerStyle = 'flex-column checkboxes-cnt form-inputs-width show';
    if (showFormType === 2) {
        checkboxesContainerStyle = 'hide'
    }

    let textareaContainerStyle = 'hide';
    if (showFormType === 2) {
        textareaContainerStyle = 'flex-column relative input-margin-bottom'
    }

    const onNameChange = (event) => {
        let name = event.target.value;
        if (name.length > 1) {
            setName(name);
        } else {
            setName('');
        }
    }

    const onEmailChange = (event) => {
        let email = event.target.value;
        let re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (re.test(email)) {
            setEmail(email);
        } else {
            setEmail('');
        }
    }

    const onQuestionChange = (event) => {
        let question = event.target.value;
        if (question.length > 3) {
            setQuestion(question);
        } else {
            setQuestion('');
        }
    }

    const getButtonStyle = () => {
        if (submitButtonClicked) {
            return ' checked';
        } else {
            return '';
        }
    }

    const clickFirstMainCheckmark = () => {
        setFirstCheckboxSelected(!firstCheckboxSelected);
    }

    const clickSecondMainCheckmark = () => {
        setSecondCheckboxSelected(!secondCheckboxSelected);
    }

    const clickThirdMainCheckmark = () => {
        setThirdCheckboxSelected(!thirdCheckboxSelected);
    }

    const submitButtonClickHandler = () => {
        if (readyToBeSubmitted) {
            setSubmitButtonClicked(!submitButtonClicked);
            setTimeout(() => {
                complete();
                setSubmitButtonClicked(false);
            }, 4500)
            if (showFormType === 1) {
                fetch('http://localhost:5000/register', {
                    method: 'post',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify({
                        name: name,
                        email: email,
                        firstDay: firstCheckboxSelected,
                        secondDay: secondCheckboxSelected,
                        thirdDay: thirdCheckboxSelected
                    })
                });
            } else if (showFormType === 2) {
                fetch('http://localhost:5000/ask', {
                    method: 'post',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify({
                        name: name,
                        email: email,
                        question: question,
                    })
                });
            }
        }
    }

    return (
        <div className={formClasses}>

            <img src={logoAbout} alt="" className="form-logo" />

            <div className="input-fields">
                <div style={{marginTop: 10}} className="flex-column relative input-margin-bottom">
                    <input type="text" className="form-inputs-width" onChange={onNameChange} required/>
                    <label>  ?</label>
                </div>

                <div className="flex-column relative input-margin-bottom">
                    <input type="text" className="form-inputs-width" onChange={onEmailChange} required/>
                    <label> </label>
                </div>

                <div className={textareaContainerStyle}>
                    <textarea type="" className="form-inputs-width" onChange={onQuestionChange} required/>
                    <label> </label>
                </div>
            </div>

            <div className={checkboxesContainerStyle}>

                <Tree name=" (27 )" click={ clickFirstMainCheckmark }>
                    <Tree name="1 " isGroupNum={true}>
                        <Tree name=" /            (10:00-11:00)" />
                        <Tree name="11:00-12:00" />
                        <Tree name="      (12:00-13:00)" />
                    </Tree>
                    <Tree name="2 " isGroupNum={true}>
                        <Tree name="10:00-11:00" />
                        <Tree name="11:00-12:00" />
                        <Tree name="12:00-13:00" />
                    </Tree>
                    <Tree name="3 " isGroupNum={true}>
                        <Tree name="10:00-11:00" />
                        <Tree name="11:00-12:00" />
                        <Tree name="12:00-13:00" />
                    </Tree>
                </Tree>

                <Tree name="  (2 )" click={ clickSecondMainCheckmark }>
                    <Tree name="1 " isGroupNum={true}>
                        <Tree name="10:00-11:00" />
                        <Tree name="11:00-12:00" />
                        <Tree name="12:00-13:00" />
                    </Tree>
                    <Tree name="2 " isGroupNum={true}>
                        <Tree name="10:00-11:00" />
                        <Tree name="11:00-12:00" />
                        <Tree name="12:00-13:00" />
                    </Tree>
                    <Tree name="3 " isGroupNum={true}>
                        <Tree name="10:00-11:00" />
                        <Tree name="11:00-12:00" />
                        <Tree name="12:00-13:00" />
                    </Tree>
                </Tree>

                <Tree name="  (3 )" click={ clickThirdMainCheckmark }>
                    <Tree name="1 " isGroupNum={true}>
                        <Tree name="10:00-11:00" />
                        <Tree name="11:00-12:00" />
                        <Tree name="12:00-13:00" />
                    </Tree>
                    <Tree name="2 " isGroupNum={true}>
                        <Tree name="10:00-11:00" />
                        <Tree name="11:00-12:00" />
                        <Tree name="12:00-13:00" />
                    </Tree>
                    <Tree name="3 " isGroupNum={true}>
                        <Tree name="10:00-11:00" />
                        <Tree name="11:00-12:00" />
                        <Tree name="12:00-13:00" />
                    </Tree>
                </Tree>
            </div>

            <div className={"button-wrapper container white full-width" + getButtonStyle()}>
                <div className={buttonStyle} onClick={submitButtonClickHandler}>
                    {showFormType === 2 ? '' : ''}
                </div>
                <div className="loader absolute loader-violet" />
                <div className="loader absolute loader-blue" />
                <div className="check-wrapper absolute">
                    <div className="checkmark" />
                </div>
            </div>
        </div>
    );
}

export default Form;