import React, { memo, useRef, useState, useEffect } from 'react';
import { useSpring, animated } from 'react-spring';

import './Tree.css';

function usePrevious(value) {
    const ref = useRef()
    useEffect(() => void (ref.current = value), [value])
    return ref.current
}

const Tree = memo(({ children, name, click=null, isGroupNum=false }) => {
    const heightRef = useRef();
    const [viewHeight, setHeight] = useState(0);

    const handleClick = () => {
        if (click) {
            click();
        }
        setOpen(!isOpen);
    }

    useEffect(() => {
        const ro = new ResizeObserver(([entry]) => {
            setHeight(entry.target.scrollHeight);
        });

        if (heightRef.current) {
            ro.observe(heightRef.current)
        }
    
        return () => {
            ro.disconnect();
        }
    }, [heightRef]);

    const [isOpen, setOpen] = useState(false)
    const previous = usePrevious(isOpen)
    const { height, opacity } = useSpring({
        from: { height: 0, opacity: 0},
        to: { height: isOpen ? viewHeight : 0, opacity: isOpen ? 1 : 0}
    });

    return (
        <div className="tree-frame">
            <div className="flex checkbox-margin-bottom">
                { isGroupNum ? <div className={ isOpen ? "checkbox white-bg checked pointer" : "checkbox white-bg unchecked pointer" } onClick={handleClick}><div class="plus-minus"></div></div>
                : 
                <div className={ isOpen ? "checkbox white-bg checked pointer" : "checkbox white-bg unchecked pointer" } onClick={handleClick}>
                    <div className="checkbox-checkmark absolute" />
                </div> }
                <p onClick={handleClick} className="pointer line-height">{name}</p>
            </div>
            
          <animated.div className="tree-context" style={{ opacity, height: isOpen && previous === isOpen ? 'auto' : height }}>
            <div ref={heightRef} children={children} />
          </animated.div>
        </div>
    );

});

export default Tree;